<template>
  <div style="margin-bottom:4.3rem;">
    <mt-loadmore
      :top-method="loadData"
      :bottom-method="loadMore"
      :auto-file="false"
      ref="loadmore"
    >
      <div class="work ju-ar al-it fl-ex fl-co" v-for="(item,index) in list" :key="index">
        <div class="line-one ju-ar al-it fl-ex fl-co">
          <img :src="item.pic" alt="图片" />
          <div class="wenzi ju-be al-it fl-ex">
            <h3>{{item.p1}}</h3>
            <p>{{item.p2}}</p>
          </div>
        </div>
        <div class="line-two ju-be al-it fl-ex fl-wr">
          <div class="one ju-be al-it fl-ex fl-co" v-for="(li,index) in item.lists" :key="index">
            <img :src="li.pic" alt="图片" />
            <div class="explain ju-be al-it fl-ex">
              <h3>{{li.p1}}</h3>
              <p>{{li.p2}}</p>
            </div>
          </div>
        </div>
      </div>
    </mt-loadmore>
    <div class="more">亲，没有更多了!</div>
  </div>
</template>

<script>
import { Loadmore } from "mint-ui";
export default {
  data() {
    return {
      list: [
        {
          pic: "../../static/img/16.png",
          p1: "青海省模范集体、劳动模范和先进工作者表彰大会在西宁...",
          p2: "2018-08-06",
          lists: [
            {
              pic: "../../static/img/17.png",
              p1: "青海省模范集体、劳..",
              p2: "2018-08-06"
            },
            {
              pic: "../../static/img/17.png",
              p1: "青海省模范集体、劳..",
              p2: "2018-08-06"
            }
          ]
        },
        {
          pic: "../../static/img/16.png",
          p1: "青海省模范集体、劳动模范和先进工作者表彰大会在西宁...",
          p2: "2018-08-06",
          lists: [
            {
              pic: "../../static/img/17.png",
              p1: "青海省模范集体、劳..",
              p2: "2018-08-06"
            },
            {
              pic: "../../static/img/17.png",
              p1: "青海省模范集体、劳..",
              p2: "2018-08-06"
            }
          ]
        }
      ]
    };
  },
  methods: {
    loadData() {
      this.allLoaded = false;
      this.bottomPullText = "上拉加载更多";
      拉取商家信息;
    },
    loadMore() {
      if (!this.allLoaded) {
        this.$axios.post();
      }
    }
  }
};
</script>

<style scoped>
.work {
  width: 100%;
  height: 22.7rem;
}
.work .line-one {
  width: 96%;
  height: 12rem;
  margin: 0 auto;
}
.work .line-one img {
  width: 100%;
  height: 10rem;
  margin: 0 auto;
}
.work .line-one .wenzi {
  width: 100%;
  height: 1.5rem;
  margin: 0 auto;
}
.work .line-one .wenzi h3 {
  color: black;
  font-size: 0.71rem;
  font-weight: bold;
}
.work .line-one .wenzi p {
  color: #8d8d8d;
  font-size: 0.4rem;
}
.work .line-two {
  width: 96%;
  height: 9rem;
  margin: 0 auto;
}
.work .line-two .one {
  width: 48.4%;
  height: 9rem;
}
.work .line-two .one img {
  width: 100%;
  height: 7.75rem;
}
.work .line-two .one .explain {
  width: 100%;
  height: 1.25rem;
}
.work .line-two .one .explain h3 {
  color: black;
  font-size: 0.71rem;
  font-weight: bold;
}
.work .line-two .one .explain p {
  color: #8d8d8d;
  font-size: 0.4rem;
}
.more {
  width: 100%;
  height: 2rem;
  text-align: center;
  line-height: 2rem;
  color: #777777;
  font-size: 0.82rem;
}
</style>


